___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: rgb(182, 169, 154);
    --primary-color-dark: rgb(75, 69, 62);
    --secundary-color:rgb(223, 208, 190);
    --third-color: rgb(182, 128, 128);
}

* {
    box-sizing: border-box;
    outline: 0;
}

body{
    margin: 0;
    padding: 0;
    background-color: var(--primary-color);
    font-family: 'Open sans', sans-serif ;
    font-size: 1.3em;
    line-height: 1.5em;
}

.container {
    border-radius: 10px;
    max-width: 640px;
    margin: 50px auto;
    background: var(--secundary-color) ;
    padding: 20px;
}

form input, form label, form button{
    display: block;
    width: 100%;
    margin-bottom: 10px;
}
form input{
    font-size: 24px;
    height: 40px;
    padding: 0 20px;
}

form input:focus{
    outline: 1px solid var(--third-color-color);
}

form button {
    border: none;
    background-color: var(--primary-color);
    color: white;
    font-size: 18px;
    font-weight: 700px;
    height: 50px;
    cursor: pointer;
    margin-top: 30px;
}

form button:hover{
    background: var(--primary-color-dark);
}

form button:active{
    color: black;
    background-color: white;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAEA;IACI,mCAAmC;IACnC,qCAAqC;IACrC,oCAAoC;IACpC,iCAAiC;AACrC;;AAEA;IACI,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sCAAsC;IACtC,qCAAqC;IACrC,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,iBAAiB;IACjB,mCAAmC;IACnC,aAAa;AACjB;;AAEA;IACI,cAAc;IACd,WAAW;IACX,mBAAmB;AACvB;AACA;IACI,eAAe;IACf,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,YAAY;IACZ,sCAAsC;IACtC,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,qCAAqC;AACzC;;AAEA;IACI,YAAY;IACZ,uBAAuB;AAC3B","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');\r\n\r\n:root {\r\n    --primary-color: rgb(182, 169, 154);\r\n    --primary-color-dark: rgb(75, 69, 62);\r\n    --secundary-color:rgb(223, 208, 190);\r\n    --third-color: rgb(182, 128, 128);\r\n}\r\n\r\n* {\r\n    box-sizing: border-box;\r\n    outline: 0;\r\n}\r\n\r\nbody{\r\n    margin: 0;\r\n    padding: 0;\r\n    background-color: var(--primary-color);\r\n    font-family: 'Open sans', sans-serif ;\r\n    font-size: 1.3em;\r\n    line-height: 1.5em;\r\n}\r\n\r\n.container {\r\n    border-radius: 10px;\r\n    max-width: 640px;\r\n    margin: 50px auto;\r\n    background: var(--secundary-color) ;\r\n    padding: 20px;\r\n}\r\n\r\nform input, form label, form button{\r\n    display: block;\r\n    width: 100%;\r\n    margin-bottom: 10px;\r\n}\r\nform input{\r\n    font-size: 24px;\r\n    height: 40px;\r\n    padding: 0 20px;\r\n}\r\n\r\nform input:focus{\r\n    outline: 1px solid var(--third-color-color);\r\n}\r\n\r\nform button {\r\n    border: none;\r\n    background-color: var(--primary-color);\r\n    color: white;\r\n    font-size: 18px;\r\n    font-weight: 700px;\r\n    height: 50px;\r\n    cursor: pointer;\r\n    margin-top: 30px;\r\n}\r\n\r\nform button:hover{\r\n    background: var(--primary-color-dark);\r\n}